html,
body {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
}

#root {
  display: -ms-flexbox;
  display: flex;
  font-family: 'Dancing Script', cursive;
  height: 100%;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  height: 100%;
  width: 100%;
}

.App-header {
  background-color: rgba(248, 230, 228, 0.9);
  height: 300px;
  color: white;
  position: relative;
  overflow: hidden;
}

.App .main_content {
  background-color: beige;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  padding: 10px;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 28px;
}

.img_wrapper {
  width: 100%;
  position: relative;
  height: 100%;
  margin: 0;
  list-style-type: none;
}

.img_wrapper img {
  height: 100%;
  width: 30%;
  position: absolute;
  background-size: cover;
  top: 0;
  left: 35%;
  margin: 0 auto;
}

@media (max-width: 1000px) {
  .img_wrapper img {
    left: 25%;
    width: 50%;
  }
}

@media (max-width: 640px) {
  .img_wrapper img {
    left: 0;
    width: 100%;
  }
}

.main_content_title {
  font-weight: bold;
  font-size: 50px;
  margin: 20px;
}

.main_content_text {
  margin-left: auto;
  margin-right: auto;
  max-width: 800px;
  line-height: 1.25;
}

.main_content_thank_you_message {
  font-weight: bold;
  margin: 20px 0;
}

.main_content_form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

@media (max-width: 640px) {
  .main_content_form {
    width: 100%;
  }
}

.main_content_form input {
  height: 20px;
  border-color: rosybrown;
  border-radius: 0;
  width: 250px;
  margin-left: 10px;
}

.main_content_form label {
  -ms-flex-preferred-size: 200px;
      flex-basis: 200px;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

@media (max-width: 640px) {
  .main_content_form label {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    -ms-flex-item-align: center;
        align-self: center;
  }
  .main_content_form input {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    -ms-flex-item-align: center;
        align-self: center;
  }
}

.main_content_form_item {
  width: 500px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
}

@media (max-width: 640px) {
  .main_content_form_item {
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}

.main_content_button {
  background-color: rosybrown;
  color: black;
  border-radius: 5px;
  padding: 20px;
  font-size: 18px;
  margin-top: 10px;
  width: 300px;
  margin-bottom: 40px;
}

.image {
  opacity: 0;
  -webkit-transition: opacity ease-in-out 2s;
  -o-transition: opacity ease-in-out 2s;
  transition: opacity ease-in-out 2s;
}

.image.image-visible {
  opacity: 1;
}

.event-list {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.main-nav {
  list-style-type: none;
  background-color: rosybrown;
  margin: 0;
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 40px;
}

.main-nav li {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 22px;
}

.main-nav li a {
  color: beige;
  text-decoration: none;
}



